import { GOOGLE_CALENDAR_CONFIG } from './googleCalendar/config';
import { supabase } from './supabase';

export interface GoogleTask {
  id: string;
  title: string;
  notes?: string;
  due?: string;
  status?: string;
  completed?: string;
  position?: string;
  taskListId: string;
  taskListTitle: string;
}

export interface GoogleEvent {
  id: string;
  summary?: string;
  description?: string;
  start: {
    dateTime?: string;
    date?: string;
    timeZone?: string;
  };
  end: {
    dateTime?: string;
    date?: string;
    timeZone?: string;
  };
}

export { checkGoogleConnection } from './googleCalendar/auth';

// Inicializa o cliente do Google API
export async function initGoogleClient(): Promise<void> {
  return new Promise((resolve, reject) => {
    if (typeof gapi === 'undefined') {
      reject(new Error('Google API Client não está carregado'));
      return;
    }

    // Obter o token de acesso do Supabase
    supabase.auth.getSession().then(({ data: { session } }) => {
      if (!session?.provider_token) {
        reject(new Error('Token do Google não disponível'));
        return;
      }

      gapi.load('client', async () => {
        try {
          await gapi.client.init({
            apiKey: GOOGLE_CALENDAR_CONFIG.apiKey,
            clientId: GOOGLE_CALENDAR_CONFIG.clientId,
            discoveryDocs: GOOGLE_CALENDAR_CONFIG.discoveryDocs,
            scope: GOOGLE_CALENDAR_CONFIG.scopes.join(' ')
          });

          // Configurar o token de acesso
          gapi.client.setToken({
            access_token: session.provider_token
          });

          resolve();
        } catch (error) {
          console.error('Erro ao inicializar cliente do Google:', error);
          reject(error);
        }
      });
    }).catch(error => {
      console.error('Erro ao obter sessão:', error);
      reject(error);
    });
  });
}

export async function getGoogleCalendarEvents(): Promise<GoogleEvent[]> {
  try {
    // Garante que o cliente está inicializado
    if (!gapi.client.calendar) {
      await initGoogleClient();
    }

    const response = await gapi.client.calendar.events.list({
      calendarId: 'primary',
      timeMin: new Date().toISOString(),
      showDeleted: false,
      singleEvents: true,
      maxResults: 100,
      orderBy: 'startTime'
    });

    return response.result.items || [];
  } catch (error: any) {
    // Detecta erro 401
    if (error.status === 401 || error?.result?.error?.code === 401) {
      throw new Error('401-unauthorized');
    }
    console.error('Erro ao buscar eventos:', error);
    throw error;
  }
}

export async function getGoogleCalendarTasks(): Promise<GoogleTask[]> {
  try {
    // Garante que o cliente está inicializado
    if (!gapi.client.tasks) {
      await initGoogleClient();
    }

    const response = await gapi.client.tasks.tasklists.list();
    const taskLists = response.result.items || [];
    
    const allTasks: GoogleTask[] = [];
    for (const taskList of taskLists) {
      const tasksResponse = await gapi.client.tasks.tasks.list({
        tasklist: taskList.id,
        showCompleted: true,
        showHidden: true
      });
      
      const tasks = tasksResponse.result.items || [];
      allTasks.push(...tasks.map(task => ({
        ...task,
        taskListId: taskList.id,
        taskListTitle: taskList.title
      })));
    }
    
    console.log('Tarefas obtidas:', allTasks);
    return allTasks;
  } catch (error) {
    console.error('Erro ao buscar tarefas:', error);
    throw error;
  }
}

export async function createGoogleCalendarEvent(event: {
  summary: string;
  description?: string;
  start: {
    dateTime: string;
    timeZone: string;
  };
  end: {
    dateTime: string;
    timeZone: string;
  };
}): Promise<GoogleEvent> {
  try {
    // Garante que o cliente está inicializado
    if (!gapi.client.calendar) {
      await initGoogleClient();
    }

    const response = await gapi.client.calendar.events.insert({
      calendarId: 'primary',
      resource: event
    });

    return response.result;
  } catch (error) {
    console.error('Erro ao criar evento:', error);
    throw error;
  }
}

export async function getGoogleAccessToken() {
  const { data: { session } } = await supabase.auth.getSession();
  // Tenta pegar o token diretamente
  if (session?.provider_token) return session.provider_token;
  // Procura nas identities
  const googleIdentity = session?.user?.identities?.find(i => i.provider === 'google');
  if (googleIdentity?.identity_data?.access_token) return googleIdentity.identity_data.access_token;
  // Não encontrou
  throw new Error('Token do Google não disponível');
} 